import React, { ReactElement } from 'react';
import Scrollbar from 'react-scrollbars-custom';
import styled, { css } from 'styled-components';

const RootScrollBar = styled(Scrollbar)`
  ${props => {
    const { bars } = props.theme;
    return css`
      .ScrollbarsCustom-Track {
        width: 5px !important;
        background-color: ${bars.colors.background} !important;
      }
      .ScrollbarsCustom-Thumb {
        background-color: ${bars.colors.icon} !important;
      }
    `;
  }}
`;

const Focus = styled.div`
  &:focus {
    outline: none;
  }
`;

type ScrollProps = {
  children: React.ReactNode;
};

function CustomStyledExample({ children }: ScrollProps): ReactElement {
  return (
    <RootScrollBar>
      <Focus tabIndex={0}>{children}</Focus>
    </RootScrollBar>
  );
}

export default CustomStyledExample;
